#root {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
}

#rows-grid {
  grid-template-rows: 20px 6px 1fr;
  height: 100%;
  display: grid;
}

#cols-grid {
  grid-template-columns: 1fr 6px 1fr;
  height: 100%;
  display: grid;
  overflow: hidden;
}

#left-panel {
  height: 100%;
  overflow: scroll;
}

#right-panel {
  height: 100%;
  position: relative;
  overflow: scroll;
}

#row-gutter {
  cursor: pointer;
  grid-area: 2 / 1 / auto / -1;
}

#col-gutter {
  cursor: col-resize;
  grid-area: 1 / 2 / -1;
}

#editor, .cm-editor {
  height: 100%;
}

#preview {
  box-sizing: border-box;
  min-width: 200px;
  max-width: 980px;
  margin: 0 auto;
  padding: 45px;
}

@media (width <= 767px) {
  #preview {
    padding: 15px;
  }
}

@media print {
  * {
    -webkit-print-color-adjust: exact;
    print-color-adjust: exact;
  }
}
